<template>
  <div class="container">
    <HeadTop :title="$t('index.挖礦記錄')"></HeadTop>
    <div class="mining">
      <van-list v-model:loading="loading" :finished="finished" :immediate-check="false" :finished-text="$t('index.沒有更多了')" @load="onLoad">
        <div class="ul" v-for="(item,index) in list" :key="index">
          <div class="li">
            <div class="key">{{$t('index.挖礦所得')}}</div>
            <div class="value">{{Number(item.reward/10**18).toFixed(4)}}UV</div>
          </div>
          <div class="li">
            <div class="key">{{$t('index.领取时间')}}</div>
            <div class="value">{{$dateformat(item.time*1000, 'yyyy-mm-dd HH:MM:ss')}}</div>
          </div>
        </div>
        <div v-if="!list.length" class="noDate">{{$t('index.暫無更多數據')}}</div>
      </van-list>
    </div>
  </div>
</template>
<script>
import HeadTop from '@/components/HeadTop'
import { reactive, toRefs, computed, watch } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import apolloProvider from '@/utils/apollo';
import gql from "graphql-tag";
// import config from "@/utils/config";
export default {
  components: {
    HeadTop
  },
  setup () {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      $t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      balance: 0,
      loading: false,
      finished: false,
      limit: 6,
      page: 1,
      list: [],
      initContract: () => {
        data.initData()
      },
      initData: () => {
        data.getList()
      },
      onLoad: async () => {
        data.page = data.page + 1
        await data.getList()
      },
      getList: () => {
        data.loading = false
        apolloProvider.clients.uvStakeClient.query({
          query: gql`query rewardPaidLists($first: Int!, $skip: Int!,$user: Bytes!) {
            rewardPaidLists(first: $first, skip: $skip, where: {user: $user}) {
              id
              reward
              user
              time
            }
          }`,
          variables: {
            first: data.limit,
            skip: (data.page - 1) * data.limit,
            user: data.address.toLowerCase()
          },
          fetchPolicy: 'no-cache'
        }).then((res) => {

          let list = res.data.rewardPaidLists
          if (list.length < data.limit) {
            data.finished = true;
          }
          data.list = [...data.list, ...list]

          data.loading = false
        })
      }
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.mining {
  padding: 16px;
  .ul {
    padding: 13px 16px;
    background: #fff;
    border-radius: 12px;
    margin-bottom: 12px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  .li {
    line-height: 1.3;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    word-break: keep-all;
    padding-bottom: 9px;
    &:last-child {
      padding-bottom: 0;
    }

    .key {
      font-size: 14px;
      color: @grey-color;
      flex-shrink: 0;
    }
    .value {
      font-size: 14px;
      color: @c3;
    }
  }
}
</style>
<style lang="less">
</style>
